.container{margin:0 1em;padding:0.5em 1em;background:#fff;border-radius:2px;white-space:nowrap;flex:1;box-shadow:0 7.5px 17.5px rgba(50,50,93,0.1),0 2.5px 7.5px rgba(0,0,0,0.07);transition-property:transform,box-shadow;transition-duration:.44s}.container:hover{cursor:pointer;transform:translateY(-2px);box-shadow:rgba(50,50,93,0.1) 0 9px 17.5px,rgba(0,0,0,0.07) 0 4px 7.5px}.header::after{content:'';display:block;height:1px;background-image:linear-gradient(90deg, #070707, transparent)}.text{font-variant:small-caps;font-size:0.9em;line-height:1.25;display:inline-block}.stat{color:#070707;display:block;letter-spacing:-1px;font-size:1.25em;margin-top:0.25em;font-family:monospace;font-weight:600;text-align:center;padding:0 3em}



